import moment from "moment"
import { Badge, OverlayTrigger, Tooltip, Card, Image } from "react-bootstrap"
import { Link } from "react-router-dom"
import { connect } from 'react-redux';
import { Component } from "react"
import { baseUrl, FontAwesomeIcon, ImagePlaceholder } from "../../../../../Assets/AssetsHelper";
import NoCardData from "../../CommonComponents/NotFound/NoCardData";
import SkTopPosts from "./SkeletonComponents/SkTopPosts";
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from "../../../../../utils";
import withRouter from "../../../../routes/WithRouter";


class TopPosts extends Component {
    onErrorImageUpdate = event => {
        event.target.src = ImagePlaceholder
    }
    render() {
        const { topPost, loader } = this.props
        return <Card className="card-stretch gutter-b">
            <div className="f-between p-3 py-2 py-sm-3 p-sm-5 border-bottom">
                <div className="h5  mb-0">
                    Top Performing Posts
                    <span className="ms-2">
                        <OverlayTrigger placement='top' overlay={<Tooltip>Your most engaging posts.</Tooltip>} >
                            <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'} /></span>
                        </OverlayTrigger>
                    </span>
                </div>
                <span className="fs-8 fw-semibold">
                    {topPost?.length > 0 ? 'CTA Clicks' : ''}
                </span>
            </div>
            <Card.Body>

                <div className="pe-2" style={{ height: 300, overflowX: 'auto' }}>
                    {loader ?
                        <SkTopPosts />
                        :
                        topPost?.length > 0 ? topPost.map((Item, Index) => <Link to={`/analytics#website`} onClick={() => localStorage.setItem('left-tabs-example-tabpane-4', 1)} key={Index} className="d-flex align-items-center px-3 py-2 btn-active-light cursor-pointer text-body active-primary">
                            <div className="d-flex align-items-center me-0 me-sm-2 mb-3 mb-sm-0 flex-grow-1 justify-content-between w-100 cursor-pointer">
                                <div className='d-flex align-items-center'>
                                    <div className="symbol symbol-50 d-flex align-items-center justify-content-center flex-shrink-0 me-2">
                                        <img className="object-center-cover" src={Item.postFileNew} height={44} width={44} alt={""} onError={this.onErrorImageUpdate} />
                                    </div>
                                    <div className="pe-2 w-100 overflow-hidden">
                                        <p className=" mb-0"><strong className="text-break text-ellipsis d-block">{Item.name}</strong></p>
                                        <div className="d-flex align-items-center">
                                            <small className="mb-0 text-gray-600 fw-semibold text-break">@{Item.username}</small>
                                            <span className="h-4px w-4px rounded-circle mx-1" style={{ backgroundColor: 'var(--bs-gray-600)' }}></span>
                                            <small className="mb-0 text-gray-600 fw-semibold text-break">{moment(new Date(Item.createdAt * 1000)).fromNow()}</small>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="f-between flex-grow-1 w-100">
                                <span className="d-flex align-items-center h-20px w-20px flex-shrink-0">
                                    <OverlayTrigger placement="top" overlay={<Tooltip>{GET_NETWORK_NAME(1)}</Tooltip>}>
                                        <Image src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(1)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(1)} data-invert={GET_NETWORK_NAME(1)} fluid/>
                                    </OverlayTrigger>
                                </span>
                                <Badge bg="secondary" className=" border fs-8 rounded-4 min-w-30px">
                                    {Item?.ctaClick}
                                </Badge>
                            </div>
                        </Link>
                        ) :
                            <NoCardData title="Find your Creators" desc="Nothing to see? Partner with top UGC creators with our Creator Collaborator program." methodTitle="Collab with Creators" method={() => this.props.navigate(`/creators`)} />
                    }
                </div>
            </Card.Body>
        </Card>
    }
}
const mapStateToProps = state => {
    return {
        topPost: state.dashboardData.top_performing_post,
        loader: state.dashboardData.top_performing_post_loader
    }
}

export default connect(mapStateToProps, null)(withRouter(TopPosts))